
import { ref,getCurrentInstance} from "vue";
import { useRoute } from "vue-router";
import { get, post } from "@/utils/request";
export default {
  name: "WinUser",
  setup() {
     const { appContext } = getCurrentInstance() as any;
    const axios = appContext.config.globalProperties.$axios;
    const onClickLeft = () => history.back();
    const userList = ref([]);
    const loading = ref(false);
    const finished = ref(false);
    const refreshing = ref(false);
    const currentPage = ref(0);
    const route = useRoute()
    const onLoad = () => {
      if (refreshing.value) {
        userList.value = [];
        currentPage.value = 0;
        refreshing.value = false;
      }
      currentPage.value++;
      console.log('goodBagId：'+route.params.id)
      let req={
        // 'pageNo':currentPage.value,
        // 'pageSize':10,
        'goodBagId':route.params.id
      }
      get(axios.defaults.baseURL +'/jeecg-boot/goodBag/api/goodBagJoinUserList',req).then(res=>{
                  if (res.data.code == 200 && res.data.result) {
          let data = res.data.result;
          console.log("luckyList", data);
          let records = data;
          if (records.length > 0) {
            for (let i = 0; i < records.length; i++) {
              userList.value.push(records[i]);
            }
          }

          console.log("当前页：" + currentPage);
          loading.value = false;
          // if (state.currentPage >= data.pages) {
            finished.value = true;
          // }
        } else {
          finished.value = true;
          loading.value = false;
        }
   
        })
     
    };
    const onRefresh = () => {
      finished.value = false;
      loading.value = true;
      onLoad();
    };
    return {
      currentPage,
      onClickLeft,
      userList,
      onLoad,
      loading,
      finished,
      onRefresh,
      refreshing,
    };
  },
};
